import { NgModule } from '@angular/core';
import { Routes, RouterModule, ChildActivationEnd } from '@angular/router';
import { LogoutComponent } from './logout/logout.component';
import { SmsLogComponent } from './sms-log/sms-log.component';
import { RecodingComponent } from './recoding/recoding.component';
import { ProfileComponent } from './profile/profile.component';
import { GpsListComponent } from './gps-list/gps-list.component';
import { ContactListComponent } from './contact-list/contact-list.component';
import { CalllogComponent } from './calllog/calllog.component';
import { PhotosComponent } from './photos/photos.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AuthComponent } from './auth/auth.component';
import { LoginComponent } from './auth/login/login.component';
import { SignupComponent } from './auth/signup/signup.component';
import { RequestPasswordComponent } from './auth/request-password/request-password.component';
import { LandingPagesComponent } from './landing-pages/landing-pages.component';


const routes: Routes = [

{
  path:'',component:LandingPagesComponent,
},
  {
   
    path: 'auth', component: AuthComponent,
    children: [
      {
        path: 'login', component: LoginComponent
      },
      {
        path: 'signup', component: SignupComponent
      },     
      {
        path: 'forgot-password', component: RequestPasswordComponent
      }
    ]
  },
  {
    path: 'dashboard', component: DashboardComponent,
    children: [
      {
        path: 'logout', component: LogoutComponent
      },
      {
        path: 'sms-log', component: SmsLogComponent
      },
      {
        path: 'recoding', component: RecodingComponent

      },
      {
        path: 'profile', component: ProfileComponent
      },
      {
        path: 'gps-list', component: GpsListComponent
      },
      {
        path: 'contact-list', component: ContactListComponent
      },
      {
        path: 'calls-log', component: CalllogComponent
      },
      {
        path: 'photos', component: PhotosComponent
      },
      {
        path: 'forgot-password', component: ForgotPasswordComponent
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
